import {
  ComponentProps,
  withStreamlitConnection,
  StreamlitComponentBase,
  Streamlit,
} from 'streamlit-component-lib'
import React, { ReactNode } from 'react'

import styles from './style.module.scss';

// Define here the available hosts
enum Platform {
  sketchup,
  rhino,
  revit
}

interface State {
  geometry: string
  identifier: any
  isPollinationModel: boolean
  platform: string
  buttonText: string
  syncToken: string
  isFocused: boolean
}

interface Message {
  Geometry: any
  Identifier: any
}

declare global {
  interface Window {
    chrome: any;
    sketchup: any;
  }
}

class GetGeometry extends StreamlitComponentBase<State> {

  public constructor(props: ComponentProps) {
    super(props)

    // Values from user input
    const isPollinationModel = this.props.args['is_pollination_model'] as boolean;
    const platform = this.props.args['platform'] as string;
    const buttonText = this.props.args['button_text'] as string;
    const syncToken = this.props.args['sync_token'] as string;
    
    let btn_txt = '';
    if (buttonText) {
      btn_txt = buttonText;
    } else {
      btn_txt = isPollinationModel ? 'Get Pollination Model' : 'Get Geometry'
    }
    
    const initialState = { 
      geometry: '', 
      identifier: '',
      isPollinationModel: isPollinationModel,
      buttonText: btn_txt,
      syncToken: syncToken,
      platform: platform.toLowerCase()
    } as State

    this.state = {
      ...initialState
    }
  }

  public render = (): ReactNode => {

    const imgStyle = {
      margin: '0px 3px 2px 0px'
    }
    
    return (
      <span>
        <button
          className={styles.poButton}
          onClick={this._onClicked}
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}
        >
          <span>
            <img style={imgStyle} 
            src={process.env.PUBLIC_URL + '/img/pollination.png'} 
            alt='logo' />
          </span>{ this.state.buttonText }
          {this.state.syncToken !== '' && ' - Sync'}
        </button>
      </span>
    )
  }

  componentDidUpdate() {
    // Empty identifier is not allowed
    if (this.state.identifier === '')
      return;
    
    // Same synctoken is not allowed
    if (this.props.args['sync_token'] 
      === this.state.syncToken )
      return;

    // Try get array GUIDs
    let stateIdentifiers = [];
    if (this.state.platform == Platform[Platform.rhino]) {
      stateIdentifiers = JSON
      .parse(this.state.identifier) as Array<string>;
    }
    else if(this.state.platform == Platform[Platform.sketchup]) {
      stateIdentifiers = this.state.identifier
    } else
    {
      // TODO: revit
      stateIdentifiers = [];
    }
    stateIdentifiers = stateIdentifiers.map(String)

    // Empty GUIDs
    if (!stateIdentifiers)
      return;
    
    // Try get the key GUID as trigger
    const id = this.props.args['sync_token']
      .split(':', 1)[0];
    
    if (stateIdentifiers.includes(id)
      && this.state.syncToken 
      !== this.props.args['sync_token'])
    {
      if (this.state.platform == Platform[Platform.rhino]) {
        this._rhinoHandlerFnc();
      }
      else if (this.state.platform == Platform[Platform.sketchup]) {
        this._skpHandlerFnc();
      }
      console.log('[POLLINATION-DEBUG]: auto-update.');
    }
  }

  private _skpHandlerFnc = (selection: boolean=false): void => {
    if (typeof window.parent.sketchup != 'undefined') 
    {
      // ruby invoke
      if (this.props.args['is_pollination_model']) {
        window.parent.sketchup.get_model(selection, {
          onCompleted: (res: string) => {
            console.log('[POLLINATION-DEBUG]: sketchup ruby done.');
            const message: Message = JSON.parse(res);
            const token = this.props.args['sync_token'] as string;
            this.setState( {...this.state,
              syncToken: token,
              geometry: message.Geometry,
              identifier: message.Identifier}, 
              () => Streamlit.setComponentValue(this.state.geometry))
          }});
      } else {
        window.parent.sketchup.get_geometry(this.props.args['key'],
          selection, {
          onCompleted: (res: string) => {
            console.log('[POLLINATION-DEBUG]: sketchup ruby done.');
            const message: Message = JSON.parse(res);
            const token = this.props.args['sync_token'] as string;
            this.setState( {...this.state,
              syncToken: token,
              geometry: message.Geometry,
              identifier: message.Identifier}, 
              () => Streamlit.setComponentValue(this.state.geometry))
          }});
      }
    }
    else {
      console.log('[POLLINATION-DEBUG]: sketchup not found.');
    }
  }

  private _rhinoHandlerFnc = (selection: boolean=false): void => {
    if (typeof window.parent.chrome !== 'undefined') {
      if (typeof window.parent.chrome.webview === 'undefined')
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
      } else {
        let res: Message = {
          Geometry: null,
          Identifier: null
        };
        const token = this.props.args['sync_token'] as string;
        if (!this.props.args['is_pollination_model']) {
          res = window.parent.chrome.webview
          .hostObjects.sync.geometry.PassJsonGeometry(
            this.props.args['key'],
            selection);
          this.setState( {...this.state, 
            syncToken: token,
            geometry: res.Geometry,
            identifier: res.Identifier}, 
            () => Streamlit.setComponentValue(this.state.geometry))
        } else {
          res = window.parent.chrome.webview
          .hostObjects.sync.hbjson.PassPollinationModel(selection);
          this.setState( {...this.state,
            syncToken: token,
            geometry: res.Geometry,
            identifier: res.Identifier}, 
            () => Streamlit.setComponentValue(this.state.geometry))       
        }
      }
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
    }
  }

  private _onClicked = (): void => {
    const platform = this.props.args['platform'] as string;
    if (platform.toLowerCase() == Platform[Platform.rhino]) { 
      this._rhinoHandlerFnc(true); 
    } else if (platform.toLowerCase() == Platform[Platform.sketchup]) {
      this._skpHandlerFnc(true);
    }
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(GetGeometry)
